html {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
}

* {
  box-sizing: border-box;
}

.container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.block {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  height: 300px;
  flex-basis: 300px;
  font-size: 100px;
  color: #fff;
  flex-wrap: wrap;
}

.item-1 {
  background-color: #f00;
  order: 1;
}

.item-2 {
  background-color: #c00;
  order: 2;
}

.item-3 {
  background-color: #900;
  order: 3;
}

.item-4 {
  background-color: #600;
  order: 4;
}
  
.item-5 {
  background-color: #300;
  order: 5;
}

.item-6 {
  background-color: #000;
  order: 6;
}

@media (min-width: 900px) {
  .item-4 {
    order: 6;
  }

  .item-6 {
    order: 4;
  }

  .block {
    flex-basis: 33.33%;
  }
}

@media (min-width: 600px) and (max-width:899px) {
  .item-3 {
    order: 4;
  }

  .item-4 {
    order: 3;
  }
}
